import Vue from 'vue'
import Vuex from 'vuex'

import DetailSettingPage from './components/main/setting_tab/DetailSettingPage.vue'
import SettingMainPage from './components/main/setting_tab/SettingMainPage.vue'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    mainPageIndex: 0,
    pageName: '서비스',
    pageStack: [SettingMainPage]
  },
  mutations: {
    pageIndexChange: function(state, payload) {
      state.mainPageIndex = payload;
      switch (payload){
      case 0:
        state.pageName = '서비스1';
        break;
      case 1:
        state.pageName = '마이페이지';
        break;
      case 2:
        state.pageName = '설정';
        break;
      }
    },
    changePageName: function(state, payload) {
      state.pageName = payload;
    },
    pushStack: function (state, payload) {
      state.pageStack.push(payload);
    }
  }
})



