<template>
  <div class="user_auth_view">
    <div class="banner_container">
      <div class="banner">
        BeBlet
      </div>
    </div>
    <div class="banner_desc_container">
      <div class="banner_desc">
        태블릿 공유 플랫폼 >
      </div>  
    </div>
    <div class="kakao_login_btn_container">
      <div class="kakao_login_btn" v-on:click="kakaoLogin">
        <img v-bind:src="'./src/assets/kakao_log.png'" class="kakao_logo" alt="">
        카카오로 로그인
      </div>
    </div>
    <div class="login_other_btn_container">
      <div class="login_other_btn">
        다른 계정으로 로그인
      </div>
    </div>
  </div>
</template>

<script>
import { serverAPI } from '@/api/index';

export default {

  data: function () {
    return {

    }
  },
  methods: {
    kakaoLogin: function () {
      let loginOptions = {
        'authTypes' : [1]
      };

      let success = async function (res) {
        // window.alert(JSON.stringify(res));
        await sendAuthInfo(res);
        // window.alert(JSON.stringify(response));
      };
      let failed = function (message) {
        window.alert(JSON.stringify(message));
      };

      KakaoCordovaSDK.login(
        loginOptions, success, failed
      );
    }
  }
}
</script>

<style>
.user_auth_view {
  background: linear-gradient(to top, #f2fcfe, #1c92d2); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  width: 100%;
  height: 100%;
}

.banner_container {
  position: absolute;
  width: 100%;
  text-align: center;
  top: 20%;
}

.banner_desc_container {
  position: absolute;
  width: 100%;
  text-align: center;
  top: 30%;
}

.banner_desc {
  color: white;
  display: inline-block;
  border: white solid 2px;
  border-radius: 20px;
  padding: 10px;
  font-family: 'Ubuntu', sans-serif;
  font-size: 15px;
}

.kakao_logo {
  position: absolute;
  left: 15%;
  top : 8px;
}

.banner {
  color: white;
  font-size: 50px;
  font-weight: 900;
  font-family: 'Ubuntu', sans-serif;
  display: inline-block;
}

.kakao_login_btn_container {
  text-align: center;
  position: absolute;
  width: 100%;
  bottom: 230px;;
}
.kakao_login_btn {
  display: inline-block;
  width: 75%;
  height: 45px;
  line-height: 45px;
  background-color: #ffe500;
  font-family: 'Gothic A1', sans-serif;
  text-align: center;
  border-radius: 5px;
  color: black;
}
.login_other_btn_container {
  text-align: center;
  position: absolute;
  width: 100%;
  bottom: 175px;
  vertical-align: center;
}
.login_other_btn {
  display: inline-block;
  width: 75%;
  height: 45px;
  line-height: 45px;
  color: white;
  font-family: 'Gothic A1', sans-serif 10px;
  background: #1c92f5; /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  border-radius: 5px;
}
.btn_text {
  vertical-align: center;

}
</style>


