<template>
  <v-ons-toolbar>
    <div class="left">
      <v-ons-back-button v-if="pageStack.length > 1">Page 1</v-ons-back-button>
    </div>
    <div class="center">
      {{ pageName }}
    </div>
  </v-ons-toolbar>
</template>

<script>
export default {
  data: function () {
    return {
      query: ''
    }
  },
  computed: {
    index: function () {
      return this.$store.state.mainPageIndex;
    },
    pageName: function () {
      return this.$store.state.pageName;
    },
    pageStack: function () {
      return this.$store.state.pageStack;
    }
  }
}
</script>

<style>
.center {
  text-align: center;
}

.search_area {
  margin-top: 10px;
}
</style>


